.navbar {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
                                
  width: 100%;
  height: 59px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  padding: 0 5% 0 5%;
  transition:0.3s;

  -webkit-backface-visibility: hidden;
  -webkit-perspective:0; 
  

  &__activeLink {
    background: #FDB719 none repeat scroll 0 0;
    height: 10px;
    width: 10px;
    border-radius: 6px;
    text-align: center;
    display: block;
    margin:auto;
    text-align: center;
    position: absolute;
    margin-top: 6%;
    left: 0;
    right: 0;
    bottom: 0;

    border: 1px dashed white;
    border-radius: 50%;


    animation: fadeInUp 1s;
    -moz-animation: fadeInUp 1s; /* Firefox */
    -webkit-animation: fadeInUp 1s; /* Safari and Chrome */
    -o-animation: fadeInUp 1s; /* Opera */
  }

  &__scrolled {
    -webkit-box-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
    box-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
    -webkit-animation-name: fadeInUp;
    animation-name: fadeInUp;
  }

  &__menuToggle {
    display: block;
    position: relative;    
    z-index: 1;
    -webkit-user-select: none;
    user-select: none;
  
    input{
      display: block;
      width: 47px;
      height: 39px;
      position: absolute;
      top: -14px;
      left: -16px;
      cursor: pointer;
      opacity: 0;
      z-index: 2;
      -webkit-touch-callout: none;
  
      &:checked {
        & ~ span {
          opacity: 1;
          transform: rotate(-45deg) translate(0px, -1px);
          background: #232323;
  
          &:nth-last-child(2){
            opacity: 0;
            transform: rotate(0deg) scale(0.2, 0.2);
          }
  
          &:nth-last-child(3){
            transform: rotate(45deg) translate(3px, 2px);
          }
        }
  
        & ~ .navigation{
          transform: none;
        }
      }
    }
  
    span {
      display: block;
      width: 22px;
      height: 2px;
      margin-bottom: 5px;
      position: relative;
      background: #000;
      border-radius: 5px;
      z-index: 1;
      transform-origin: 4px 0px;
      transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
  
      &:first-child{
        transform-origin: 0% 0%
      }
  
      &:nth-last-child(2){
        transform-origin: 0% 100%;
      }
    }
  }

  #wrap{
    width: 100%;
    display: flex;
    justify-content: space-between;
    max-width: 1100px;
    margin: 0 auto;
    align-items: center;
    height: 100%;
  }

  #logo{
    max-width: 80px;
    width: 20%;

    img{
      image-rendering: -webkit-optimize-contrast;
      opacity: 0.8;
      width: 100%;
      cursor: pointer;
    }
  }
  
  .navigation {
    position: absolute;
    background: #fff;
    top: 59px;
    left: 0;
    width: 100%;
    font-size: 15px;
    overflow: hidden;

    -webkit-font-smoothing: antialiased;
    /* to stop flickering of text in safari */
    //transform-origin: 0% 0%;
    //transform: translate(0, -550%);
    transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
    transform-origin: top;
    transform: scaleY(0);

    -moz-box-shadow: 0 3px 4px rgba(0, 0, 0, 0.25);
    -webkit-box-shadow: 0 3px 4px rgba(0, 0, 0, 0.25);
    box-shadow: 0 3px 4px rgba(0, 0, 0, 0.25 );


    ul{
      height: 100%;
      padding: 0;
      list-style-type: none;

      max-height: 0;

      li {
        position: relative;
        a:first-child {
          height: 100%;
          min-height: 34px;
          line-height: 50px;
          text-align: center;
          cursor: pointer;
          font-family: Poppins;
          font-style: normal;
          font-weight: 600;
          text-transform: uppercase;
          color:#696969;
          text-decoration: none;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 0 15px 0 15px;
        }
      }
    }

    &-open{
      transform: scaleY(1) !important;
  
      ul{
        max-height: 700px !important;
        padding-bottom: 10px;
      }
    }
  }
  
  @media (min-width: 768px) {
    height: 84px;

    &__activeLink{
      bottom: 12px;
      height: 6px;
      width: 30px;
      border: unset;
      border-radius: 6px;
    }

    li{
      margin: 0 5px 0 5px;
    }

    .active::after{
      margin-top: 44px;
    }

    .navigation {
      position: relative;
      transform: none;
      position: relative;
      transform: none;
      top:0;
      width: unset;
      font-size: 14px;
      height: 100%;

      -moz-box-shadow: unset;
      -webkit-box-shadow: unset;
      box-shadow: unset ;

      ul{
        margin:0;   
        height:100%;
        display: flex;
        max-height: unset !important;
      }

      li{
        display: inline-flex;
       // padding-left: 20px;
      }  
    }

    &__menuToggle {
      display: none;
    }
  }

  @keyframes fadeInUp {
    0% {
      opacity: 0;
      -webkit-transform: translateY(20px);
      -ms-transform: translateY(20px);
      transform: translateY(20px);
    }
    100% {
      opacity: 1;
      -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
      transform: translateY(0);
    }
  }
}